import { watch, ref, nextTick, getCurrentInstance, inject, provide } from "vue";
import { TestService } from "../utils/test";
// 获取上级
function useParent(name, r) {
    const d = getCurrentInstance();
    if (d) {
        let parent = d.proxy?.$.parent;
        if (parent) {
            while (parent && parent.type?.name != name && parent.type?.name != "cl-crud") {
                parent = parent?.parent;
            }
            if (parent) {
                if (parent.type.name == name) {
                    r.value = parent.exposed;
                }
            }
        }
    }
}
// 多事件
function useEvent(names, { r, options, clear }) {
    const d = {};
    if (!r.__ev)
        r.__ev = {};
    names.forEach((k) => {
        if (!r.__ev[k])
            r.__ev[k] = [];
        if (options[k]) {
            r.__ev[k].push(options[k]);
        }
        d[k] = (...args) => {
            r.__ev[k].filter(Boolean).forEach((e) => {
                e(...args);
            });
            if (clear == k) {
                for (const i in r.__ev) {
                    r.__ev[i].splice(1, 999);
                }
            }
        };
    });
    return d;
}
// crud
export function useCrud(options, cb) {
    const Crud = ref();
    useParent("cl-crud", Crud);
    if (options) {
        provide("useCrud__options", {
            ...options,
            service: options.service == "test" ? TestService : options.service
        });
    }
    watch(Crud, (val) => {
        if (val) {
            if (cb) {
                cb(val);
            }
        }
    });
    return Crud;
}
// 新增、编辑
export function useUpsert(options) {
    const Upsert = ref();
    useParent("cl-upsert", Upsert);
    if (options) {
        provide("useUpsert__options", options);
    }
    watch(Upsert, (val) => {
        if (val) {
            if (options) {
                const event = useEvent(["onOpen", "onOpened", "onClosed"], {
                    r: val,
                    options,
                    clear: "onClosed"
                });
                Object.assign(val.config, event);
            }
        }
    }, {
        immediate: true
    });
    return Upsert;
}
// 表格
export function useTable(options) {
    const Table = ref();
    useParent("cl-table", Table);
    if (options) {
        provide("useTable__options", options);
    }
    return Table;
}
// 表单
export function useForm(cb) {
    const Form = ref();
    useParent("cl-form", Form);
    nextTick(() => {
        if (cb && Form.value) {
            cb(Form.value);
        }
    });
    return Form;
}
// 高级搜索
export function useAdvSearch(options) {
    const AdvSearch = ref();
    useParent("cl-adv-search", AdvSearch);
    if (options) {
        provide("useAdvSearch__options", options);
    }
    return AdvSearch;
}
// 对话框
export function useDialog(options) {
    const Dialog = inject("dialog");
    watch(() => Dialog?.fullscreen.value, (val) => {
        options?.onFullscreen(val);
    });
    return Dialog;
}
