import { inject, reactive, isRef, useSlots } from "vue";
import { isFunction } from "../utils";
export function useRefs() {
    const refs = reactive({});
    function setRefs(name) {
        return (el) => {
            refs[name] = el;
        };
    }
    return { refs, setRefs };
}
export function useGlobal() {
    return inject("globalOptions");
}
export function useTools() {
    const browser = inject("browser");
    const global = useGlobal();
    const slots = useSlots();
    function getValue(data, params) {
        if (isRef(data)) {
            return data.value;
        }
        else {
            if (isFunction(data)) {
                return data(params);
            }
            else {
                return data;
            }
        }
    }
    return { browser, ...global, slots, getValue };
}
export function useCore() {
    const crud = inject("crud");
    const mitt = inject("mitt");
    return {
        crud,
        mitt
    };
}
export function useElApi(keys, el) {
    const apis = {};
    keys.forEach((e) => {
        apis[e] = (...args) => {
            return el.value[e](...args);
        };
    });
    return apis;
}
export function useConfig({ props }) {
    const config = reactive(props);
    function setConfig(data) {
        Object.assign(config, data);
    }
    return { setConfig, ...config };
}
export function useEventListener(name, cb) {
    window.removeEventListener(name, cb);
    window.addEventListener(name, cb);
    cb();
}
