import mitt from "mitt";
const ev = mitt();
class Mitt {
    id;
    constructor(id) {
        this.id = id || 0;
    }
    send(type, name, ...args) {
        // @ts-ignore
        ev[type](`${this.id}__${name}`, ...args);
    }
    on(name, ...args) {
        this.send("on", name, ...args);
    }
    emit(name, ...args) {
        this.send("emit", name, ...args);
    }
    off(name, ...args) {
        this.send("off", name, ...args);
    }
}
export default Mitt;
